<template>
  <div class="flex column content-start">
    <q-date
      title="Календарь"
      mask="YYYY-MM-DD" 
      v-model="dateRange"
      @input="updateDataWithPeriod"
      range
    />
    <q-btn
      flat
      color="primary"
      label="Показать все транзакции"
      @click="clearFilter"></q-btn>
  </div>
</template>

<script>
import { date as quasarDate } from 'quasar'
import store from "@/store/index.js";

  export default {
    updated() {
      console.log('isBetweenDate',quasarDate.isBetweenDates('2022-03-25', this.dateRange?.from,this.dateRange?.to))
    },
    data() {
      return {
        dateRange: quasarDate.formatDate(Date.now(), 'YYYY-MM-DD')
      }
    },
    methods: {
      async updateDataWithPeriod () {
        // const periodData = {
        //   start_date: this.dateRange?.from,
        //   end_date: this.dateRange?.to
        // }
        try{
          store.state.transactions.datePickerTransactions = store.state.transactions.transactions
          const arr = store.state.transactions.transactions
            .filter(item => quasarDate.isBetweenDates(item.transaction_date, this.dateRange?.from,this.dateRange?.to, { inclusiveFrom: true, inclusiveTo: true }))
            console.log('arr', arr)
          store.state.transactions.transactions = arr
          store.dispatch('updateCategories')
        // await this.$store.dispatch("getTransactions", periodData)
        // await this.$store.dispatch("getCategories", periodData)
        } catch (e) {
          console.log('updateDataWithPeriod error', e)
        }
      },
      async clearFilter () {
        try{
          store.state.transactions.transactions = store.state.transactions.datePickerTransactions
          store.dispatch('updateCategories')
        // await this.$store.dispatch("getTransactions", {})
        // await this.$store.dispatch("getCategories", {})
        } catch (e) {
          console.log('clearFilter error', e)
        }
      }
    }
  }
</script>

