<template>
<div>
  <div class="flex widgets-wrapper column flex-center">
    <widget
      v-for="(item, index) in widgetsList"
      :key="item.id"
      :currentIndex="index + widgetsFrameCurrentFirst * 2 + 1"
      v-bind="item"/>
    <add-widget-buttons
      @next-widgets-frame="widgetsFrameCurrentFirst += 1"
      @previous-widgets-frame="widgetsFrameCurrentFirst -= 1"
      :nextDisable="nextDisableButton"
      :previousDisable="previousDisableButton" />
  </div>
      <BaseModal
        widget
        :showModal.sync="$store.state.widgets.showModalAddWidget"
        :optionsSelect="options"
        title="Добавить виджет"
        btnLabel="Добавить"
        v-bind.sync="widget"
        :categoryId.sync="widget.category"
        @click="addWidget"
        @clearModalValue="clearModalValue"
         />
</div>
</template>

<script>
import Widget from '../components/Widget.vue'
import AddWidgetButtons from '../components/AddWidgetButtons.vue'
import BaseModal from './BaseComponents/BaseModal.vue'

const initialWidget = () => ({
        category: null,
        limit: null,
        validity: null,
        criterion: null,
        color: null
})

export default {
  mounted() {
    this.$store.state.widgets.widgets = [
      {
    "id": 0,
    "category": {
      "id": 0,
      "name": "Кросовки",
      "category_type": "income"
    },
    "limit": "1000.00",
    "validity": "day",
    "criterion": "less",
    "color": "#E5F5FF",
    "transactions_sum": "5.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
  {
    "id": 1,
    "category": {
      "id": 1,
      "name": "Машины",
      "category_type": "income"
    },
    "limit": "5.00",
    "validity": "day",
    "criterion": "more",
    "color": "#E4F6E8",
    "transactions_sum": "2.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
  {
    "id": 2,
    "category": {
      "id": 2,
      "name": "Чизбургеры",
      "category_type": "income"
    },
    "limit": "100.00",
    "validity": "day",
    "criterion": "less",
    "color": "#FFF0E9",
    "transactions_sum": "10.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
  {
    "id": 3,
    "category": {
      "id": 3,
      "name": "Жизнь",
      "category_type": "income"
    },
    "limit": "5000.00",
    "validity": "day",
    "criterion": "less",
    "color": "#F7F4FF",
    "transactions_sum": "3200.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
  {
    "id": 4,
    "category": {
      "id": 4,
      "name": "Окна",
      "category_type": "income"
    },
    "limit": "1000.00",
    "validity": "day",
    "criterion": "more",
    "color": "#FFE5E5",
    "transactions_sum": "700.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
  {
    "id": 5,
    "category": {
      "id": 5,
      "name": "Все",
      "category_type": "income"
    },
    "limit": "10000.00",
    "validity": "day",
    "criterion": "less",
    "color": "#E4E8F6",
    "transactions_sum": "500.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
    {
    "id": 6,
    "category": {
      "id": 6,
      "name": "Же",
      "category_type": "income"
    },
    "limit": "10020.00",
    "validity": "day",
    "criterion": "less",
    "color": "#FCE9FF",
    "transactions_sum": "10.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
    {
    "id": 7,
    "category": {
      "id": 7,
      "name": "Па",
      "category_type": "income"
    },
    "limit": "400.00",
    "validity": "day",
    "criterion": "less",
    "color": "#EDEDED",
    "transactions_sum": "200.00",
    "created_at": "2022-03-25",
    "expire_at": "2022-03-25"
  },
    ]
  },
  components: {
    Widget, AddWidgetButtons, BaseModal
  },
  data() {
    return {
      widget: initialWidget(),
      widgetsFrameCurrentFirst: 0,

    }
  },
  computed: {
    options() {
      return this.$store.state.categories.categories
      .map(item => ({
          label: item.name,
          value: item.id,
          category: item.category_type
      }))
      .filter(item => item.category == 'expense')
    },
    widgetsList() {
      return this.$store.state.widgets.widgets.slice(this.widgetsFrameCurrentFirst*2, this.widgetsFrameCurrentFirst*2 + 5)
    },
    widgetsFrameLast() {
      return Math.ceil(this.$store.state.widgets.widgets.length/2)
    },
    previousDisableButton() {
      return this.widgetsFrameLast < 3 || this.widgetsFrameCurrentFirst === 0
    },
    nextDisableButton() {
      return this.widgetsFrameCurrentFirst + 2 >= this.widgetsFrameLast
    }
  },
  methods:{
    async addWidget() {
      console.log('this.widget', this.widget)
      try{
        await this.$store.dispatch("addWidget", this.widget)
      } catch (e) {
        console.log('addWidget error', e)
      }
    },
    clearModalValue() {
      this.widget = initialWidget()
    }
  }  
}
</script>

